<template>
  <div class="bg-white px-[20px] py-[16px]">
    <PageTitleWithGoBackButton>
      Markazga Xodim qo'shish
    </PageTitleWithGoBackButton>
    <section class="p-2">
      <AddWorkerToCenterForm @submit="onSubmit" />
    </section> 
    <ModalComponent
      ref="modalComponentRef"
      modalClass="!w-auto"
      modal-position="center"
      :close-on-click-away="false"
      :hide-close-button="true"
    >
      <template #slotBody>
        <div class="p-10 text-3xl flex items-center justify-center">
          <h1>Xodim markazga muvaffaqiyatli qo'shildi</h1>
          <Icon icon="mdi:check-circle" class="mx-2 text-primary" width="34" height="34" />
        </div>
        <div class="flex justify-center">
          <button
            class="base_input border-primary bg-primary text-white mx-2 hover:opacity-80"
            @click="modalComponentRef.closeModal"
          >
            Davom etish
          </button>
          <button
            class="base_input border-primary bg-primary text-white mx-2 hover:opacity-80"
            @click="$router.push('/')"
          >
            Bosh sahifa
          </button>
        </div>
      </template>
    </ModalComponent>
  </div>
</template>


<script setup>
import {ref} from 'vue';
import axios from "../services/api/axios";
import ModalComponent from '../components/Common/Modal/ModalComponent';
import {Icon} from '@iconify/vue';
import useAlert from "../composables/useAlert";
import AddWorkerToCenterForm from "../components/AddWorkerToCenterForm";
import PageTitleWithGoBackButton from "../components/LayoutItems/PageTitleWithGoBackButton";
import useErrorAlert from "../composables/useErrorAlert";

const {alertThisError} = useErrorAlert()


const {alertIt} = useAlert()


const modalComponentRef = ref()

const onSubmit = (async (formData) => {
  try {
    const {message} = await axios.post(`/auth/userCreateForDirektor/`, formData)
    await alertIt(message,'success')
    modalComponentRef.value.openModal()
  } catch (e) {
    alertThisError(e)
  }
});

</script>

<style scoped>
span {
  @apply text-red-400 h-5
}
</style>
